.intro{
    display: flex;
    height: 77vh;
    margin-top: 6rem;
}
.i-Left{
    display: flex;
    position: relative;
    flex: 1;
    flex-direction: column;
    gap: 2rem;    
}


.i-name{
    flex: 1;   
    flex-direction: column;
}

.i-name>:nth-child(1){
    color: var(--black);
    font-weight: bold;
    font-size:3rem ;
    
}

.i-name>:nth-child(3){
    color: var(--orange);
    font-weight: bold;
    font-size:3rem ;
    
    
}

.i-name>:nth-child(4){
   
    color: var(--gray);
    margin-top: 10px;
    font-size:10 px ;
    
    
}

.i-button{
    width: 6rem;
    height: 2rem;
}

.i-icons{
    margin-top: 3rem;
    display: flex;
    gap: 0rem;

}
.i-icons>*{
    transform: scale(0.5);

}
.i-icons>*:hover{
    cursor: pointer;
}

.i-right{
    flex: 1;
    position: relative;
}

.i-right>*{
    position: absolute;
    z-index: 1;

}
.i-right>:nth-child(1){
    transform:scale(0.80);
    left: -15%;
    top: 9%;
}

.i-right>:nth-child(2){

    top: -4.6remp;
    transform:scale(0.70);
    left: -3rem;
}

.i-right>:nth-child(3){

    transform:scale(0.28);
    left: -109%;
    top: -80%;
    
}

.i-right>:nth-child(4){

    transform:scale(0.6);
    top: -19%;
    left: -24%;
    border-radius: 50%;
    padding: 0;
}
.blur{
    position:absolute;
    width: 22rem;
    height: 14rem;
   
    border-radius: 50%;
    background: #edd0ff;
    z-index: -9;
    top:- 18%;
    left: 56%;
    filter: blur(72px);
}
@media screen and (max-width:480px){
    .intro{
        flex-direction: column;
        height: 64rem;
        gap: 6rem;
    }
    .i-right{
        transform: scale(0.6);
        left: -5rem;
    }
    .i-right .blur{
        display: none;
        left: -40rem;
    }
  
    .floating-div:nth-of-type(1){
        top:-7rem !important;


    }
}